.welcome {
  display: flex;
  flex-flow: column;
  margin-top: 0;
  min-height: 90%;
  background-image: url("../img/intro.jpg");
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("../img/intro.jpg");
  background-position: center;
  background-size: cover;

  @media screen and (min-width: 992px) {
    padding: 0em 15% 0em;
  }

  &__wrapper {
    display: flex;
    flex-flow: column;
    height: 50%;
    margin: auto;
    align-self: center;
    padding-top: 2em;

    @media screen and (min-width: 992px) {
      flex-flow: row;
    }
  }

  &__text,
  &__video {
    width: 100%;
  }

  &__text {
    display: flex;
    align-items: center;

    @media screen and (min-width: 992px) {
      width: 60%;
    }

    @media screen and (min-width: 540px) and (max-width: 768px) {
      margin-bottom: 6em;
    }

    h1 {
      padding: 1em;
      color: $color_text-light;
      line-height: 1.4;
    }
  }

  &__video {
    @media screen and (min-width: 992px) {
      width: 40%;
    }

    iframe {
      width: 100%;
      height: 100%;

      min-height: 240px;

      @media screen and (min-width: 540px) and (max-width: 768px) {
        min-height: 310px;
      }
    }
  }

  .cta__container {
    margin: auto;

    @media screen and (max-width: 992px) {
      display: none;
    }

    @media screen and (min-width: 992px) {
      width: 100%;
    }

    @media screen and (min-width: 1500px) {
      width: 60%;
    }

    .button {
      @media screen and (min-width: 992px) {
        width: 45%;
      }
    }
  }
}
